@import "../Variables/Variable.scss";

.topbar {
  height: 50px;
  width: 100%;
  background-color: $main;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;

  .topLeft {
    flex: 3;
    .logo {
      font-size: 24px;
      margin-left: 20px;
      font-weight: bold;
      color: white;
      cursor: pointer;
    }
  }

  .topCenter {
    flex: 5;

    .searchBar {
      width: 100%;
      height: 30px;
      background-color: white;
      border-radius: 30px;
      display: flex;
      align-items: center;

      svg {
        font-size: 25px !important;
        margin-left: 10px;
      }

      input {
        border: none;
        width: 70%;
        outline: none;
      }
    }
  }

  .topRight {
    flex: 4;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;

    .topbarLinks {
      span {
        margin-right: 10px;
        font-size: 14px;
        cursor: pointer;
      }
    }

    .topbarIcons {
      display: flex;

      .topbarIconItem {
        margin-right: 15px;
        cursor: pointer;
        position: relative;

        span {
          width: 15px;
          height: 15px;
          background-color: red;
          border-radius: 50%;
          color: white;
          position: absolute;
          top: -5px;
          right: -5px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 12px;
        }
      }
    }

    .userProfileLogout {
      display: flex;
      align-items: center;
      img {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        object-fit: cover;
        cursor: pointer;
      }

      span {
        margin-left: 1rem;
        font-size: 20px;
        font-weight: 500;
        cursor: pointer;
        display: flex;
        align-items: center;
      }
    }
  }
}
